/**
 * Main menu
 *
 * @package Embark
 * @since   1.0.0
 */

.main-menu {

	.menu-item {
		position: relative;

		// Menu item trigger button element.
		&__trigger {
			background: none;
			border: none;
			border-radius: 0;
			box-shadow: none;
			display: inline-block;
			color: $primary-color;
			font-size: rem(20);
			padding: 0 rem(5);
			position: relative;
				z-index: 2;
			text-align: center;
			transition-property: background-color, border-color;

			&::before {
				@include fa-icon;

				@include mobile-menu {
					content: $fa-var-angle-down;
				}

				@include desktop-menu {
					content: $fa-var-angle-right;
				}
			}

			&:focus {
				outline-offset: -2px;
				outline-width: 1px;
				outline-style: solid;
			}

			.screen-reader-text {
				outline: none;
			}
		}

		// When a parent menu is open, style the trigger button.
		&.open {

			> .menu-item__trigger {
				background-color: rgba(black, .05);
				// border-left: rem(1) solid rgba(black, .02);
				color: $mobile-nav-item-2-open-color;

				&::before {

					@include mobile-menu {
						content: $fa-var-angle-up;
					}

					@include desktop-menu {
						content: $fa-var-angle-right;
					}
				}
			}
		}
	}

	.menu-item[class*="current-menu-"] > a {
		color: $primary-color;
	}

	/**
	 * Mobile Menu
	 */
	@include mobile-menu {

		@include jacket(non-critical) {

			// All menu-items.
			.menu-item {
				font-size: rem(16);

				a {
					padding: rem($mobile-nav-item-vertical-padding) rem($mobile-nav-item-horizontal-padding);
				}

				&__trigger {
					height: rem(60);
					padding: rem($mobile-nav-item-vertical-padding);
					position: absolute;
						top: 0;
						right: 0;
						z-index: 10;
					width: rem($mobile-nav-item-trigger-width);
				}

				&-has-children {

					> a {
						padding-right: rem($mobile-nav-item-trigger-width + $mobile-nav-item-horizontal-padding);
						position: relative;

						&::after {
							content: " ";
							border-left: rem(1) solid $mobile-nav-border-color;
							position: absolute;
							top: 0;
							right: rem($mobile-nav-item-trigger-width);
							bottom: 0;
						}
					}
				}

				&.open {

					> a {
						background-color: $mobile-nav-item-2-open-bgc;
						color: $mobile-nav-item-2-open-color;
					}

					> .menu-item__trigger {
						background-color: rgba(black, .05);
						// border-left: rem(1) solid rgba(black, .02);
						color: $mobile-nav-item-2-open-color;
					}
				}
			}

			// All sub-menus.
			.sub-menu {
				display: none;
				padding: 0;

				.menu-item {
					border-top: rem(1) solid $mobile-nav-border-color;
				}

				// Current-menu item color in sub-menus.
				.current-menu-item > a {
					color: $primary-color;
				}

				> .menu-item {

					> a {
						padding-left: rem($mobile-nav-item-horizontal-padding * 2);
					}
				}

				.sub-menu {

					> .menu-item {

						> a {
							padding-left: rem($mobile-nav-item-horizontal-padding * 3);
						}
					}
				}
			}

			// Top-level menu items only.
			> .menu-item {
				background-color: #fff;
				border-bottom: rem(1) solid $mobile-nav-border-color;

				// Active state.
				&.open {

					> a {
						background-color: $mobile-nav-item-1-open-bgc;
						color: $mobile-nav-item-1-open-color;
					}

					> .menu-item__trigger {
						color: #fff;
					}
				}
			}
		}
	}

	/**
	 * Desktop Menu
	 */
	 @include desktop-menu {

		a {
			display: block;
		}

		// All parent menu-items.
		.menu-item-has-children {

			> a {
				padding-right: rem($menu-item-trigger-size-desktop + 10);
			}
		}

		// All menu-item triggers.
		.menu-item__trigger {
			height: rem($menu-item-trigger-size-desktop);
			width: rem($menu-item-trigger-size-desktop);
			position: absolute;
				top: 50%;
				right: rem($menu-item-horizontal-padding-desktop);
				z-index: -1; // z-index behind link, so mouse users can't click it but keyboard users can tab to it.
			transform: translateY(-50%);
		}

		// All menu-items.
		.menu-item {
			display: inline-block;

			a {
				position: relative;
					z-index: 2;
			}

			@include jacket(non-critical) {

				&.open ,
				&:hover ,
				.current-menu-item {

					> a {
						color: $header-link-hover-color;
					}

				}

				&.open ,
				&:hover {

					> a {
						color: $header-link-hover-color;
					}

					> .sub-menu {
						opacity: 1;
						visibility: visible;
					}
				}

				&.submenu-offscreen {

					.sub-menu {
						left: auto;
						right: calc(100% + #{rem($submenu-tier-2-gap)});
						transform: none;

						&::before {
							left: 100%;
							right: auto;
							transform: translateY(-50%);
						}
					}

					&.menu-item-has-children {

						&:hover {

							> a {

								&::before {
									left: auto;
									right: 100%;
								}
							}
						}
					}
				}
			}
		}

		// All sub-menus.
		.sub-menu {
			// Note: this is a fix for users who resize their screen from mobile to desktop.
			// Overrides any inline `display: none` rules that may have been applied in mobile
			// view from jQuery slideToggle method. For desktop view, visibility/opacity is
			// used to show/hide menus, so `display: block` is ok.
			display: block !important;
			opacity: 0;
			padding: 0;
			position: absolute;
			visibility: hidden;

			@include jacket(non-critical) {
				background-color: $submenu-bgc;
				border-radius: rem($radius);
				width: rem($submenu-width);
				top: 0;
				left: calc(100% + #{rem($submenu-tier-2-gap)});
				z-index: $zindex-nav;
				transition-property: opacity;

				&::before {
					content: " ";
					border-top: rem($submenu-arrow-size) solid transparent;
					border-bottom: rem($submenu-arrow-size) solid transparent;
					border-left: rem($submenu-arrow-size) solid $submenu-arrow-color;
					width: 0;
					height: 0;
					position: absolute;
					top: 50%;
					right: 100%;
					transform: translateY(-50%) rotate(180deg);
				}

				// All sub-menu menu-items.
				.menu-item {
					display: block;
					font-size: rem($base-font-size);
					padding: rem($menu-item-vertical-padding-desktop) rem($menu-item-horizontal-padding-desktop);

					&:first-child {
						padding-top: rem($menu-item-vertical-padding-desktop * 2);

						.menu-item__trigger {
							top: calc(50% + #{rem($menu-item-vertical-padding-desktop / 2)});
						}
					}

					&:last-child {
						padding-bottom: rem($menu-item-vertical-padding-desktop * 2);

						.menu-item__trigger {
							top: 50%;
						}
					}
				}

				// Only sub-menu menu-items parents.
				.menu-item-has-children {

					&:hover {

						> a {
							position: static !important;

							&::before {
								content: " ";
								width: rem($submenu-tier-2-gap);
								position: absolute;
								top: 0;
								left: 100%;
								bottom: 0;
							}
						}
					}
				}
			}
		}

		// Top-level menu-items only.
		> .menu-item {

			> .menu-item__trigger {
				right: 0;

				&::before {
					content: $fa-var-angle-down;
				}
			}

			+ .menu-item {
				margin-left: $gap-lg;
			}

			@include jacket(non-critical) {

				&::after {
					content: " ";
					display: none;
					height: calc(100% + #{rem($submenu-tier-1-gap)});
					position: absolute;
					top: 0;
					right: 0;
					left: 0;
					z-index: 1;
				}

				> .sub-menu {
					top: calc(100% + #{rem($submenu-tier-1-gap)});
					left: 50%;
					right: auto;
					transform: translateX(-50%);

					&::before {
						content: " ";
						border-left: rem($submenu-arrow-size) solid transparent;
						border-right: rem($submenu-arrow-size) solid transparent;
						border-bottom: rem($submenu-arrow-size) solid $submenu-arrow-color;
						width: 0;
						height: 0;
						position: absolute;
						top: auto;
						right: auto;
						bottom: 100%;
						left: 50%;
						transform: translateX(-50%);
					}
				}

				&:hover {

					&::after {
						display: block;
					}
				}

				&.open {

					> .menu-item__trigger {

						&::before {
							content: $fa-var-angle-up;
						}
					}
				}
			}
		}
	}
}
