/**
 * Menu Base
 *
 * Resets ul and li styles. 
 * Adds additional styles that all menus need.
 *
 * @package Embark
 * @since   1.0.0
 */

// Removes margins and padding on ul element.
.menu {
	margin: 0;
	padding: 0;

	// Remove margins and padding on listitems. Font-size should inherit from menu style.
	.menu-item {
		font-size: inherit;
		list-style: none;
		margin: 0;
		padding: 0;

		// Links.
		a {
			display: block;
			text-decoration: none;
		}

	}

	// Removes nested margins and paddings.
	.sub-menu {
		margin: 0;
		padding: 0;
	}

}