/**
 * Helper class: external-link
 *
 * This class can be applied to any link that needs the added
 * indication to the user that its linking out the user off site.
 * Out-of-the-box, this class will add a font awesome external link icon.
 *
 * @see     config/mixins/_media-queries.scss        Media query mixins.
 * @see     config/variables/_media-sizes.scss       Media query variables.
 * @see     config/variables/components/_header.scss Logo variables.
 *
 * @package Embark
 * @since   1.0.0
 */

.external-link {
	position: relative;
	@include jacket( non-critical ) {
		&:after {
			@include fa-icon;
			content: $fa-var-external-link;
			padding-left: rem(6);
			//opacity: .6;
		}
	}
}