//---
// WordPress Generated Classes
//
// @ref: https://codex.wordpress.org/CSS
//---

.aligndefault,
.site-content > .alignnone {
	@include aligndefault;

	.aligndefault {
		padding-left: 0;
		padding-right: 0;
	}
}

.alignwide {
	@include alignwide;
}

.alignfull {
	@include alignfull;
}

.aligncenter {
	display: block;
	margin: 0 auto $gap-md;
	text-align: center;

	&--mobile {

		@include breakpoint(xs, max) {
			text-align: center !important;
		}
	}

	&--tablet {

		@include breakpoint(sm, max) {
			text-align: center !important;
		}
	}
}

.alignleft {
	text-align: left;

	&--mobile {

		@include breakpoint(xs, max) {
			text-align: left !important;
		}
	}

	&--tablet {

		@include breakpoint(sm, max) {
			text-align: left !important;
		}
	}
}

.alignright {
	text-align: right;

	&--mobile {

		@include breakpoint(xs, max) {
			text-align: right !important;
		}
	}

	&--tablet {

		@include breakpoint(sm, max) {
			text-align: right !important;
		}
	}
}

.alignleft,
.alignright {
	display: block;
	margin-bottom: $gap-md;
}

.alignleft img {
	display: block;
	float: left;
	margin-right: 2em;
}

.alignright img {
	display: block;
	float: right;
	margin-left: 2em;
}

.alignright figcaption,
.alignleft figcaption {
	font-size: 75%;
	color: gray;
	margin-bottom: 1em;
}

.alignleft figcaption {
	clear: left;
	float: left;
	margin-right: 2em;
}

.alignright figcaption {
	clear: right;
	float: right;
	margin-left: 2em;
}

.aligncenter {

	img {
		margin-left: auto;
		margin-right: auto;
	}
}

@include breakpoint(xs) {

	.alignleft,
	.alignright {
		max-width: 60% !important;
		position: relative;
		top: rem(5);

		&.btn {
			float: none;
			min-width: 100% !important;
			margin-right: 0;
			margin-left: 0;
			position: static;
		}
	}

	.alignleft {
		float: left;
		margin-right: rem(30);
	}

	.alignright {
		float: right;
		margin-left: rem(30);
	}
}

@include breakpoint(sm) {

	.alignleft,
	.alignright {
		max-width: calc(100% - #{rem(300)}) !important;

		&.btn {
			max-width: 100% !important;
		}
	}
}

.full-width {
	width: 100%;
	height: auto;
}
