/**
 * Custom logo link
 *
 * The logo link as output from WordPress.
 *
 * @see     config/variables/components/_header.scss Logo variables.
 *
 * @package Embark
 * @since   1.0.0
 */

// Custom logo wrapper is used to size and position the logo.
.custom-logo-wrapper {
	transition-property: width;
	width: 100%;
	max-width: rem($logo-width);
}

.custom-logo-link {
	display: block;
	height: 0;
	width: 100%;
	padding-bottom: $logo-aspect-ratio;
	position: relative;

	img,
	svg {
		display: block;
		height: 100%;
		width: 100%;
		position: absolute;
	}
}

