$mobile-nav-width: 320;
$mobile-nav-background: #fff;
$mobile-nav-padding: $gap-px * 2;
$mobile-nav-item-vertical-padding: $gap-px;
$mobile-nav-item-horizontal-padding: $gap-px;
$mobile-nav-item-1-open-bgc: $secondary-color;
$mobile-nav-item-1-open-color: #fff;
$mobile-nav-item-2-open-bgc: $gray-xl;
$mobile-nav-item-2-open-color: $primary-color;
$mobile-nav-item-trigger-width: 50;
$mobile-nav-border-color: rgba(black, .05);

$submenu-bgc: $gray-xl;
