/**
 * BUTTONS
 *
 * @since 1.0.0
 */

// Button Text modifiers.
.#{$btn-class}-text {
	@extend %#{$btn-class}-text;

	// With arrow.
	@include jacket( non-critical ) {
		&--arrow:after {
			@include fa-icon;
			content: $fa-var-angle-right;
			margin-left: rem(7);
		}
	}
}


/**
 * Base button styles, and extensible placeholder.
 *
 * @since 1.0.0
 */
.#{$btn-class},
.block-btn {
	@extend %#{$btn-class};

	&--sm {
		@extend %#{$btn-class}--sm;
	}

	&--lg,
	&--large {
		@extend %#{$btn-class}--lg;
	}

	&--block {
		@extend %#{$btn-class}--block;
	}

	// With arrow.
	@include jacket( non-critical ) {

		&--arrow {
			&:after {
				@include fa-icon;
				content: $fa-var-angle-right;
				margin-left: rem(7);
			}
		}
	}
}

.block-btn {
	margin-bottom: 0;
}

.btn--no-caps {
	text-transform: none !important;
}

/**
 * Style buttons and inputs with btn class correctly.
 */
// sass-lint:disable no-qualifying-elements
button.btn ,
input[type="button"].btn {
	box-shadow: none;
}

/**
 * Diabled buttons
 *
 * @see ../config/variables/components/_buttons.scss for $btn-solid-variants variable
 *
 * @since 1.0.0
 */
button.btn[disabled] ,
input[type="button"][disabled] ,
.btn--disabled {
	background-color: $gray-l !important;
	border-color: $gray-d !important;
	color: $gray !important;
	cursor: not-allowed;
	pointer-events: none;

	&:hover {
		background-color: $gray-l !important;
		border-color: $gray-d !important;
		color: $gray !important;
	}
}
// sass-lint:enable no-qualifying-elements


/**
 * Solid Buttons
 *
 * @see ../config/variables/components/_buttons.scss for $btn-solid-variants variable
 *
 * @since 1.0.0
 */
@each $modifer, $options in $btn-solid-variants {

	$class: "#{$btn-class}--#{$modifer}";

	@if $modifer == "neutral" {
		$class: "#{$btn-class}";
	}

	.#{$class},
	.block-btn.has-#{$modifer}-background-color {
		@extend %#{$class};
	}
}

/**
 * Outline Buttons
 *
 * @see ../config/variables/components/_buttons.scss for $btn-outline-variants variable
 *
 * @since 1.0.0
 */
@each $modifer, $options in $btn-outline-variants {

	$class: "#{$btn-class}--#{$modifer}-o";

	.#{$class},
	.block-btn--outline.has-#{$modifer}-background-color {
		@extend %#{$class};
	}
}

/**
 * Simple Buttons
 *
 * @since 1.0.0
 */
.block-btn--simple {

	.block-btn__hover {
		padding-right: 1.2em !important;
	}

	svg {
		display: inline-block;
		height: 1em;
		width: 1em;
		position: relative;
		top: -.1em;
		transform: none;
		vertical-align: middle;
	}
}

@each $modifier, $color, $nouse in $swatches {
	.block-btn--simple.has-#{$modifier}-background-color {
		background: none !important;
		border: none !important;
		color: $color !important;
		padding: 0;
		text-align: inherit;

		&:hover,
		&:focus {
			background: none !important;
			color: darken($color, 10%) !important;
			text-decoration: underline !important;
		}
	}
}

.btn--arrow-icon {
	display: inline-block;
	height: rem(54);
	width: rem(54);

	[stroke] {
		stroke: currentColor;
		transition-property: stroke;
	}
}

.block-btn {

	&__hover {
		background: none !important;
		border: none !important;
		border-radius: 0 !important;
		color: inherit !important;
		cursor: pointer;
		padding: 0 !important;
		transition: none !important;
	}
}
