.wp-block-pullquote {
    text-align: center;

	&.alignleft,
	&.alignright {
		max-width: 305px !important; // Needs variable.

		p {
			font-size: 150%;
		}
	}

	blockquote {
		border-left: none;
		padding-top: 70px;
		padding-left: 0;

		&::before {
			font-size: rem(72);
			left: 50%;
			top: 0;
			transform: translateX(-50%);
			
		}
	}

	p {
		font-size: 250%;
		line-height: 1.6;
	}

	cite,
	footer {
		position: relative;
	}

	.has-text-color a {
		color: inherit;
	}

	&:not(.is-style-solid-color) {
		background: none;
	}

	&.is-style-solid-color {
		border: none;
		
		blockquote {
			margin-left: auto;
			margin-right: auto;
			max-width: 60%;
			text-align: left;
		}

		p {
			margin-top: 0;
			margin-bottom: 0;
		}

		cite {
			color: inherit;
			font-style: 400;
			text-transform: none;
		}
	}
}