// Card element variables.
$card-background: white;
$card-border: 1px solid rgba(black, .125);
$card-border-radius: $radius; //0 rem(10) rem(18) rgba( $secondary-color, 0 );
$card-box-shadow: null;

// Card media variables.
$card-media-border-radius: $radius;
// sizes to consider:
// 16-9 landscape (for resources/stock photos)
// 4-3 landscape  (for leadership/headshots)

// Card body variables.
$card-body-padding-base: rem(20);
$card-body-padding-sm: rem(30);

// Card footer variables.
$card-footer-background-color: rgba( black, .05 );
$card-footer-border-top: $card-border;
$card-footer-padding-base: rem(20);
$card-footer-padding-sm: rem(20) rem(30);

/**
 * Card Pattern
 *
 * Defines a set of classes to apply to specific markup intended for
 * displaying posts in a card format.
 *
 * @package  Embark
 * @since    1.0.0
 */
.card {
	background: $card-background;
	background-clip: content-box;
	border: $card-border;
	@if ( $card-border-radius ) {
		border-radius: rem($card-border-radius);
	}
	@if ( $card-box-shadow ) {
		box-shadow: $card-box-shadow;
	}
	color: $base-font-color;
	display: flex;
	flex-direction: column;
	position: relative;
	text-decoration: none;
	transition-property: background-color, border-color, box-shadow, color;

	// Card media.
	&__media {
		height: rem(250);
		@if ( $card-media-border-radius ) {
			border-top-left-radius: rem($card-media-border-radius);
			border-top-right-radius: rem($card-media-border-radius);
		}
		overflow: hidden;
		position: relative;

		// Default size: 16:9 ratio.
		&:before {
			content: "";
			display: block;
			padding-bottom: percentage( 9 / 16 );
		}

		// Modifier class: 4:3 ratio.
		&--4x3:before {
			padding-bottom: percentage( 3 / 4 );
		}

		img {
			position: absolute;
				top: 0;
				left: 0;
		}
	}

	// Card body. The container for all content in the card (excluding the card__footer).
	&__body {
		flex: 1 1 auto;
		padding: $card-body-padding-base;
		@if ( $card-body-padding-sm ) {
			@include breakpoint(sm) {
				padding: $card-body-padding-sm;
			}
		}
	}

	// Excerpt. Placed inside card__body. Container for paragraph elements.
	&__excerpt {
		color: $base-font-color;
		transition-property: border-color;
	}

	// Label. Intended to have an h6 style.
	&__label {
		@extend %h6; // from elements/_headings.scss
		color: rgba( $gray-xd, $muted );
	}

	// Button text. This may be a span tag, so it needs to be styled like a link when card is hovered.
	.btn-text {
		color: $primary-color;
	}

	// Footer. Aligns to the bottom of the card.
	&__footer {
		border-top: 1px solid $gray-l;
		padding: $card-footer-padding-base;
		@if ( $card-footer-padding-sm ) {
			@include breakpoint(sm) {
				padding: $card-footer-padding-sm;
			}
		}
	}

	@include jacket(non-critical) {

		// Allow for all these props to be transitioned.
		&__label ,
		&__headline ,
		&__excerpt ,
		.btn-text {
			transition-property: color;
		}

		&__footer {
			transition-property: border-color;
		}

		// Hover style.
		&:hover {
			background-color: $primary-color;
			color: white;

			.btn-text {
				color: white;
			}

			.card {
				&__label ,
				&__headline ,
				&__excerpt {
					color: white;
				}

				&__footer {
					border-color: rgba( white, .3 );
				}
			}

		}

	}

}
