/**
 * Meta
 *
 * A list of meta information such as post-date, author, category, etc.
 *
 * @package  Embark
 * @since    1.0.0
 */

// Meta element, usually an unordered list element.
.meta {
	font-size: rem(14);
	line-height: ( 22 / 14 );
	margin: em(8) 0 em(16);
	padding: 0;

	li , 
	li li:first-child {
		padding-top: 0;
	}

	li {
		display: inline-block;
		padding-left: 0;
		position: relative;

		&:before {
			content: "\2022";
			color: $ul-bullet-color;
			display: inline-block;
			position: absolute;
				left: rem($list-indent-amount * .5);
			text-shadow: 0 0 1px $primary-color;
			width: rem($list-bullet-width);
		}

		&:first-child:before {
			content: none;
		}

		&:before {
			left: rem(-16);
		}

		+ li {
			margin-left: em(26);
		}
	}
}