/**
 * Media Object Pattern
 *
 * Defines a set of classes to apply to specific markup intended for
 * listing posts.
 *
 * @package  Embark
 * @since    1.0.0
 */

.media-object {

	// Label. Intended to have an h6 style.
	&__label {
		@extend %h6; // from elements/_headings.scss
		color: $gray;
		margin: rem(12) 0;
	}

	// Headline. Intended for h3 elements.
	&__headline {
		margin: rem(8) 0 rem(2);

		+ .btn-text {
			display: inline-block;
			margin-top: rem(16);
		}

		.external-link:after {
			font-size: 80%;
			opacity: .6;
		}
	}

	// Excerpt. Intended for paragraph elements.
	&__excerpt {
		margin: rem(16) 0 0;
	}

	&__media {
		margin-bottom: rem($gutter);
		max-width: rem(150);
		// Float image to right
		@include breakpoint(sm) {
			float: right;
			margin-left: $gap-lg;
			max-width: rem(200);
		}
	}

}
