.masthead {
	@include block-spacing-bottom;
	background-color: $gray-xd;
	color: #fff;
	position: relative;

	&__media {
		opacity: .4;
		overflow: hidden;
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;

		img,
		img[data-src].loaded,
		video {
			@include object-fit(cover);
			height: 100%;
			width: 100%;
		}
	}

	&__content-wrapper,
	&__content {
		display: flex;
		flex-direction: column;
		justify-content: center;
	}

	&__content-wrapper {
		position: relative;
	}

	&__content {
		padding-top: rem(30);
		padding-bottom: rem(30);
		text-align: center;
	}

	&__title {
		@include fluid-type(get-breakpoint(sm), get-breakpoint(lg), 40, 60);
		color: #fff;
		margin: 0;

		&:only-child {
			margin: 0;
		}
	}

	.search-form-wrapper {
		display: table;
		margin: rem(8) auto;
	}

	&--image {

		.masthead__content {
			min-height: rem(300);
		}
	}

	&--video {

		.masthead__content {
			min-height: rem(300);

			@include breakpoint(sm) {
				min-height: rem(450);
			}
		}
	}
}
