.pagination {
	display: flex;
	margin: rem(30) rem(-16);
	position: relative;

	.dashicons {
		line-height: 1.4;
	}

	@include breakpoint(sm, max) {
		flex-wrap: wrap;
		justify-content: center;
		margin: rem(30) rem(-8);

		.page-numbers {
			font-size: rem(16);
			margin: 0 rem(8);
		}

		.prev,
		.next {
			font-size: 0;
		}

		.prev {
			margin-right: $gap-md;
		}

		.next {
			margin-left: $gap-md;
		}

		.dashicons {
			font-size: rem(18);
			line-height: rem(28);
		}
	}
}

.page-numbers {
	@extend %#{$btn-class}-text ;
	margin: 0 rem(16);
}
