.wp-block-separator {
	
	&::before {
		width: rem(200);
	}

	&.is-style-wide {
		
		&::before {
			width: auto;
			max-width: rem($content-width-wide);
		}

		&.alignfull {

			&::before {
				max-width: 100%;
			}
		}
	}

	&.is-style-dots {
		text-align: center;

		&::before {
			content: "\00b7 \00b7 \00b7";
			border: none;
			color: $gray;
			font-family: serif;
			font-size: rem(25);
			letter-spacing: 2em;
			line-height: 0;
			padding-left: 2em;
		}
	}
}
