.social-navigation {

	li {
		display: inline-block;
		list-style: none;
	}

	a {
		display: inline-block;
	}

	.screen-reader-text {
		clip: rect(1px, 1px, 1px, 1px);
		height: 1px;
		overflow: hidden;
		position: absolute !important;
		width: 1px;
		word-wrap: normal !important;
	}

	.icon {
		display: inline-block;
		fill: currentColor;
		height: 20px;
		overflow: hidden;
		position: relative;
		vertical-align: top;
		width: 20px;
	}
}

.social-media-menu {
	margin-bottom: 0;
}
