/**
 * Base Blog Styles
 *
 * @package Embark
 * @since   1.0.0
 */

.blog {

	.site-content {

		.page-header {
			@extend %alignwide;
			padding-right: rem($site-gutter-xs);
			padding-left: rem($site-gutter-xs);

			@include breakpoint(alignwide) {
				max-width: rem( $content-width-wide );
				padding-right: 0;
				padding-left: 0;
			}
		}

		.media-list {
			margin-top: 0;
		}

		.media-object {
			display: flex;

			> div {
				flex-basis: 100%;
			}

			&__media {
				float: none;
				margin: 0;
				max-width: rem(250);
				padding-left: 20px;
				order: 2;
			}

			&__body {
				max-width: 100%;
				order: 1;
			}

		}
	}

}
