//---
// BasicLightbox
// The default CSS for a lightbox plugin.
// @ref: https://github.com/electerious/basicLightbox
//---

@include jacket(non-critical) {

	.basicLightbox {
		max-width: 100vw;

		&__placeholder {
			padding: rem(32) 15px 0;
		}

		&__image {
			position: relative;
		}

		&__video {
			width: 80vw;
			max-width: rem(980);
		}

		&__close {
			color: #fff;
			cursor: pointer;
			position: absolute;
			top: 0;
			right: rem(15);
			transition: opacity .3s ease-in-out;

			&:hover {
				opacity: .5;
			}

			.dashicons,
			.dashicons-before:before {
				font-size: rem(28);
			}
		}

		&__prev,
		&__next {
			background: rgba(black, .7);
			border: none;
			color: #fff;
			cursor: pointer;
			padding: rem(5);
			position: absolute;
			top: 50%;
			left: 0;
			transform: translateY(-50%);
			transition-property: background;

			.dashicons {
				height: auto;
				width: auto;
			}

			&:hover {
				background: $primary-color;
			}
		}

		&__next {
			left: auto;
			right: 0;
		}

		img {
			max-height: 80vh;
			max-width: 80vw;
		}

		iframe {
			max-width: 100%;
		}
	}
}
