/**
 * Grid placeholder class
 *
 * Progressively enhances flexbox to CSS Grid.
 *
 * Applies flexbox fallback for IE11 support first, followed by
 * @supports rules for display: grid capabilities.
 *
 * Caniuse indicates IE11 support, but this is only partial support.
 * IE11 does not support grid-gap and you have to write out the columns
 * for -ms-grid-columns.
 *
 * All other browsers that support display: grid will override the flexbox
 * styling with CSS Grid.
 *
 * @package  Embark
 * @since    1.0.0
 */

%grid {
	// Breakpoint for when grid items are stacked in single column.
	@include breakpoint(grid, max) {
		> * + * {
			margin-top: rem( 20 );
		}
	}
	// Breakpoint for when grid items begin row layouts.
	@include breakpoint(grid) {
		display: flex;
		flex-wrap: wrap;
		margin-right: rem( -($gutter / 2) );
		margin-left: rem( -($gutter / 2) );

		> * {
			display: flex;
			flex: 1 0 50%;
			margin-top: rem( $gutter / 2 );
			margin-bottom: rem( $gutter / 2 );
			max-width: 50%;
			padding-right: rem( $gutter / 2 );
			padding-left: rem( $gutter / 2 );
			position: relative;

			// Item inside grid__item.
			> * {
				width: 100%;
			}
		}

		// Grid Support.
		@supports ( display: grid ) {
			display: grid;
			grid-gap: rem( $gutter );
			margin-right: initial;
			margin-left: initial;
			width: 100%;

			> * {
				margin-top: 0;
				margin-bottom: 0;
				max-width: none;
				padding-right: initial;
				padding-left: initial;
			}

			&--halves ,
			&--thirds ,
			&--quarters ,
			&--fifths {
				-ms-grid-columns: 1fr 1fr;
				grid-template-columns: repeat(2, 1fr);
			}
		}

	}

	@include breakpoint(md) {

		&--thirds {
			> * {
				flex: 1 0 33%;
				max-width: 33%;
				padding-right: rem( $gutter / 2 );
				padding-left: rem( $gutter / 2 );
			}
		}

		&--quarters {
			> * {
				flex: 1 0 25%;
				max-width: 25%;
				padding-right: rem( $gutter / 2 );
				padding-left: rem( $gutter / 2 );
			}
		}

		&--fifths {
			> * {
				flex: 1 0 20%;
				max-width: 20%;
				padding-right: rem( $gutter / 2 );
				padding-left: rem( $gutter / 2 );
			}
		}

		// Grid Support.
		@supports ( display: grid ) {

			&--thirds ,
			&--quarters ,
			&--fifths {
				> * {
					max-width: none;
					padding-right: initial;
					padding-left: initial;
				}
			}

			&--thirds {
				-ms-grid-columns: 1fr 1fr 1fr;
				grid-template-columns: repeat(3, 1fr);
			}

			&--quarters {
				-ms-grid-columns: 1fr 1fr 1fr 1fr;
				grid-template-columns: repeat(4, 1fr);
			}

			&--fifths {
				-ms-grid-columns: 1fr 1fr 1fr 1fr 1fr;
				grid-template-columns: repeat(5, 1fr);
			}

		}

	}
}
