.wp-has-aspect-ratio {

	.wp-block-embed__wrapper {
		height: 0;
		position: relative;

		> * {
			@include absolute-fill;
			height: 100%;
			width: 100%;
		}
	}
}

.wp-embed-aspect {

	&-16-9 {

		.wp-block-embed__wrapper {
			padding-top: percentage(9/16);
		}
	}

	&-4-3 {

		.wp-block-embed__wrapper {
			padding-top: percentage(3/4);
		}
	}
}