/**
 * Site header component
 *
 * @see     config/mixins/_media-queries.scss        Media query mixins.
 * @see     config/variables/_media-sizes.scss       Media query variables.
 * @see     config/variables/components/_header.scss Site header variables.
 *
 * @package Embark
 * @since   1.0.0
 */

.site-header {
	background-color: #fff;
	color: $header-text-color;
	display: flex;
	height: rem( $header-height-base );

	@if $sticky-header {
		flex-shrink: 0;
		position: sticky;
		top: 0;
		z-index: $zindex-nav;

		.ie & {
			&.js-is-sticky {
				width: 100%;
			}
		}
	} @else {
		position: relative;
	}

	&__inner {
		align-items: center;
		display: flex;
		flex: 1 0 auto;
		justify-content: space-between;
		width: 100%;

		@include mobile-menu {
			flex: 1 0 0%;
		}
	}

	a {
		color: inherit;
	}

	// Navicon when placed in `.site-header`.
	.navicon {
		position: absolute;
			top: 50%;
			right: rem( $header-navicon-offset-right );
		transform: translateY(-50%);
	}

	// Site navigation container (nav-drawer).
	&__navigation {
		position: absolute;
			top: rem( $header-height-base );
			right: 0;
			left: 0;
		width: 0;
	}

	.html--menu-open &__navigation {
		width: auto;
	}

	// Tablet portrait up.
	@include breakpoint(sm) {
		height: rem( $header-height-sm );

		&__navigation {
			top: rem( $header-height-sm );
		}
	}

	// Mobile Menu.
	@include mobile-menu {
		z-index: $zindex-nav;

		&__navigation {
			background: white;
			border-top: rem(1) solid $gray;
			height: calc( 100vh - #{rem( $header-height-sm )});
			overflow: auto;
			padding-bottom: rem( $header-height-sm );
			z-index: $zindex-nav;

			&-top {
				padding: $gap-md;
			}

			.search-form label {
				width: 100%;
			}

			@include breakpoint(sm, max) {
				height: calc( 100vh - #{rem( $header-height-base )});
				padding-bottom: rem( $header-height-base );
			}
		}
	}

	// Tablet landscape / desktop menu up.
	@include desktop-menu {
		height: rem( $header-height-md );

		.navicon {
			display: none;
		}

		&__navigation {
			align-items: flex-end;
			display: flex;
			flex-direction: column;
			position: static;
				top: auto;
				right: auto;
				left: auto;
			width: auto;

			&-top {
				align-items: center;
				display: flex;
				margin-bottom: $gap-md;
			}
		}
	}

}

// Jump link for navigation.
.skip-to-content {

	// Make the skip to content link visiable when it gets focus.
	&:focus {
		background: $gray-xl;
		box-shadow: 0 0 rem(2) rem(2) $gray;
		clip: auto;
		clip-path: none;
		font-weight: 700;
		height: auto;
		padding: rem(10) rem(20);
		position: absolute;
			top: rem(5);
			left: rem(5);
			z-index: 10000;
		width: auto;
	}
}
