.navicon {
	appearance: none;
	background: none;
	border: none;
	display: block;
	height: rem($navicon-height);
	padding: 0;
	position: relative;
	width: rem($navicon-width);

	@include jacket(non-critical) {
		transition-property: background, width;

		&:focus {
			outline: none;

			.navicon__trigger {
				background: $navicon-focus-color;

				&:before,
				&:after {
					background: $navicon-focus-color;
				}
				&--open {
					background: none;
				}
			}
		}
	}

	&__trigger {
		background: $navicon-color;
		display: block;
		position: absolute;
			top: 50%;
			left: 0;
		transform: translateY(-50%);
		width: 100%;

		@include jacket(non-critical) {
			transition-property: background;
		}

		&,
		&:before,
		&:after {
			border-radius: rem($navicon-bar-radius);
			height: rem($navicon-bar-height);
		}

		&:before,
		&:after {
			background: inherit;
			content: "";
			width: 100%;
			position: absolute;
			left: 0;
		}

		&:before {
			top: rem(-$navicon-bar-height * $navicon-bar-spacing);

			@include jacket(non-critical) {
				transition-property: top, transform;
			}
		}

		&:after {
			top: auto;
			bottom: rem(-$navicon-bar-height * $navicon-bar-spacing);

			@include jacket(non-critical) {
				transition-property: bottom, transform;
			}
		}

		@include jacket(non-critical) {

			&--open {
				background: none;
				width: rem($navicon-width - 4);

				&:before {
					top: 0;
					transform: rotate(45deg);
				}

				&:after {
					bottom: 0;
					transform: rotate(-45deg);
				}

				&:after,
				&:before {
					background: $navicon-active-color;
				}
			}
		}
	}
}
