/**
 * Blog Author Card Styles
 *
 * @package Embark
 * @since   1.0.0
 */

 .author {
	&-card {
		margin: $gap-xl 0;
	}

	&-header {
		text-align: center;
	}

	&-image,
	&-name {
		display: inline-block;
		vertical-align: middle;
	}

	&-description {
		margin: $gap-md;
		text-align: center;
	}

	&-social {
		ul,
		li {
			list-style: none;
			padding: 0;
			text-align: center;
		}

		li {
			border-right: $border-gray;
			display: inline-block;
			line-height: 0;
			padding: 0 $gap-md;
			vertical-align: middle;

			&:last-child {
				border: none;
			}
		}

		a {
			font-size: 0;
		}

		svg {
			max-height: rem(24);
			max-width: rem(24);
			width: 100%;
		}
	}

 }