/**
 * Comments
 *
 * @since 1.0.0
 */

.comments {

	&__list {
		padding: 0;
		
		> li {
			padding: 0;
		}

		li {
			list-style: none;

			&:before {
				content: none;
			}
		}

		.comment-respond {
			border-bottom: $border-gray-l;
			padding: $gap-lg 0;
		}
	}

}

.comment {
	border-bottom: $border-gray-l;
	padding: $gap-lg 0;
	
	&__gravatar {
		float: left;
		margin: 0;
		max-width: 20%;
	}

	&__author {
		margin-bottom: $gap-sm;
	}
	
	&__meta {
		color: $gray;
		margin: 0 0 $gap-md;

		li,
		li:first-child {
			padding: 0;
		}
	}

	&__content {
		margin: 0 0 $gap-md;
	}

	&__body {
		float: left;
		padding-left: $gap-xl;
		max-width: 80%;
		width: 100%;
	}

	&__reply {

		i {
			margin-right: rem(10);
		}

		i,
		a {
			color: $gray;
			text-decoration: none;
		}
	}

	&__replies-count {
		color: $gray;
		float: right;
	}

}