.object-fit {

	// For creating object-fit classes for position.
	$positions-x: left, center, right;
	$positions-y: top, center, bottom;

	&--cover {
		@include object-fit(cover);
	}

	&--contain {
		@include object-fit(contain);
	}

	// cover positions
	@each $y in $positions-y {
		&--#{$y} {
			@each $x in $positions-x {
				&-#{$x} {
					&.object-fit--cover {
						@include object-fit(cover, $y $x);
					}
					&.object-fit--contain {
						@include object-fit(contain, $y $x);
					}

				}
			}
		}
	}
}

[class*="object-fit"] {
	width: 100%;
	height: 100%;
}
