@mixin block-spacing-top {
	margin-top: $block-spacing--base;

	@include breakpoint(md) {
		margin-top: $block-spacing--md;
	}
}

@mixin block-spacing-bottom {
	margin-bottom: $block-spacing--base;

	@include breakpoint(md) {
		margin-bottom: $block-spacing--md;
	}
}

@mixin block-spacing {
	@include block-spacing-top;
	@include block-spacing-bottom;
}

@mixin negative-block-spacing($pos: "top") {
	@if ($pos == "top") {
		margin-top: -$block-spacing--base;

		@include breakpoint(md) {
			margin-top: -$block-spacing--md;
		}
	} @else {
		margin-bottom: -$block-spacing--base;

		@include breakpoint(md) {
			margin-bottom: -$block-spacing--md;
		}
	}
}
