/**
 * Jacket wrapper keyword
 * @link https://github.com/at-import/jacket
 *
 * Used to target non-critical styles.
 */
$jacket: non-critical, editor;

 /**
  * Config
  */
 @import "config/functions/_em.scss";
@import "config/functions/_map-deep-get.scss";
@import "config/functions/_rem.scss";
@import "config/functions/_str-replace.scss";
@import "config/functions/_strip-unit.scss";
 @import "config/variables/___paths.scss";
@import "config/variables/__colors.scss";
@import "config/variables/__layout.scss";
@import "config/variables/__typography.scss";
@import "config/variables/_border-radius.scss";
@import "config/variables/_borders.scss";
@import "config/variables/_box-shadow.scss";
@import "config/variables/_transitions.scss";
@import "config/variables/_zindex.scss";
 @import "config/variables/components/_button.scss";
@import "config/variables/components/_form-elements.scss";
@import "config/variables/components/_header.scss";
@import "config/variables/components/_logo.scss";
@import "config/variables/components/_main-menu.scss";
@import "config/variables/components/_navicon.scss";
 @import "config/mixins/_align.scss";
@import "config/mixins/_block-spacing.scss";
@import "config/mixins/_clearfix.scss";
@import "config/mixins/_fluid-type.scss";
@import "config/mixins/_jacket.scss";
@import "config/mixins/_media-queries.scss";
@import "config/mixins/_position.scss";
@import "config/mixins/_site-gutter.scss";
 @import "config/extends/_align.scss";
@import "config/extends/_buttons.scss";
@import "config/extends/_grid.scss";
@import "config/extends/_pretty-inputs.scss";

 /**
  * Fonts
  */
 @import "fonts/_fonts.scss";

/**
* Node Modules
*/

// Font Awesome
// @link https://www.npmjs.com/package/font-awesome
$fa-font-path: "//netdna.bootstrapcdn.com/font-awesome/4.7.0/fonts";
@import "../../node_modules/font-awesome/scss/font-awesome";

// Slick Carousel
// @link https://www.npmjs.com/package/slick-carousel
@import "../../node_modules/slick-carousel/slick/slick.scss";

// Object Fit Images
// @link https://www.npmjs.com/package/object-fit-images
@import "../../node_modules/object-fit-images/preprocessors/mixin.scss";

// Basiclightbox
// @link https://www.npmjs.com/package/basiclightbox
@import "../../node_modules/basiclightbox/src/styles/main.scss";

// Fix Chrome 77 issue with overlapping Meta Boxes.
.block-editor-writing-flow { 
    height: auto;  
}

// Block title style.
.editor-post-title__block {
	
	.editor-post-title__input {
		@extend %h1;
		@include font-family;
	}
}

.wp-block {
	@include font-family;
	max-width: rem($max-content-width);

	&[data-align="wide"] {
		max-width: rem($content-width-wide);
	}

	&[data-align="full"] {
		max-width: none;
	}

	/**
	* HTML Elements
	*/
	@import "elements/blockquote";
	@import "elements/figure";
	@import "elements/headings";
	@import "elements/horizontal-rule";
	@import "elements/img";
	@import "elements/inline-elements";
	@import "elements/links";
	@import "elements/lists";
	@import "elements/paragraphs";
	@import "elements/tables";

	/**
	* Reusable Components
	*/
	@import "components/_breadcrumbs.scss";
@import "components/_buttons.scss";
@import "components/_card.scss";
@import "components/_comments.scss";
@import "components/_custom-logo-link.scss";
@import "components/_em-image.scss";
@import "components/_footer.scss";
@import "components/_header.scss";
@import "components/_icon-list.scss";
@import "components/_masthead.scss";
@import "components/_media-object.scss";
@import "components/_meta.scss";
@import "components/_navicon.scss";
@import "components/_page-header.scss";
@import "components/_pagination.scss";
@import "components/_play-btn.scss";
@import "components/_site-content.scss";
@import "components/_site.scss";
@import "components/_social.scss";
@import "components/blocks/__base.scss";
@import "components/blocks/_block-cover-image.scss";
@import "components/blocks/_block-embed.scss";
@import "components/blocks/_block-file.scss";
@import "components/blocks/_block-gallery.scss";
@import "components/blocks/_block-image.scss";
@import "components/blocks/_block-list.scss";
@import "components/blocks/_block-pullquote.scss";
@import "components/blocks/_block-quote.scss";
@import "components/blocks/_block-separator.scss";
@import "components/blocks/_block-video.scss";
@import "components/blog/_author-card.scss";
@import "components/blog/_blog-sidebar.scss";
@import "components/blog/_blog.scss";
@import "components/forms/_search-form.scss";
@import "components/lists/_media-lists.scss";
@import "components/menus/_default-menu.scss";
@import "components/menus/_main-menu.scss";
@import "components/menus/_social-menu.scss";
@import "components/menus/_utility-menu.scss";
@import "components/widgets/_widget-cta-text.scss";
@import "components/widgets/_widget-recent-posts.scss";

	/**
	* Third-Party Plugins
	*/
	@import "plugins/_basiclightbox.scss";
@import "plugins/_gravity-forms.scss";

	/**
	* Helper Classes (Overrides)
	*/
	@import "helpers/_alignment.scss";
@import "helpers/_clearfix.scss";
@import "helpers/_collapse-margin.scss";
@import "helpers/_external-link.scss";
@import "helpers/_grid.scss";
@import "helpers/_loaded.scss";
@import "helpers/_no-button-style.scss";
