@import "helpers/_no-list-style.scss";
@import "helpers/_object-fit.scss";
@import "helpers/_responsive-video.scss";
@import "helpers/_screen-reader-text.scss";
@import "helpers/_text-and-bg-colors.scss";
@import "helpers/_text-sizes.scss";
@import "helpers/_wp-caption.scss";
}

ul[class*="acf"] {

	li {

		&::before {
			display: none;
		}
	}
}

// Editor Sidebar Styles
.editor-post-featured-video {
	margin-top: 2em;

	.components-button {
		display: block;
	}
}
