// Wraps all content-blocks. Targets all children.
.site-content {

	> * {

		&:not(.alignwide):not(.alignfull) {
			@include aligndefault;
		}

		&:last-child {
			margin-bottom: $gap-lg;

			&.wp-block-spacer {
				height: 0 !important;
			}

			@include breakpoint(sm) {
				margin-bottom: $gap-xl;
			}
		}
	}
}

.single-news-item,
.single-event {

	.site-content {

		> p,
		> h1,
		> .h1,
		> h2,
		> .h2,
		> h3,
		> .h3,
		> h4,
		> .h4,
		> h5,
		> .h5,
		> h6,
		> .h6 {
			@include aligndefault;
		}
	}
}
