@include jacket(non-critical) {

	/* Prevents img without src to appear */
	img[data-src] {
		opacity: 0;
		transition: opacity $transition-duration $transition-duration 0s, transform $transition-duration $transition-duration 0s, visibility 0s linear $transition-duration;
		visibility: hidden;

		&.loaded {
			opacity: 1;
			transition: opacity $transition-duration, transform $transition-duration, visibility 0s linear;
			visibility: visible;
		}
	}
}
