// Gallery Variables
$gallery-gap: $gap-md;
$float-max-width: rem(310);
$figcaption-text-color: #fff;
$figcaption-gradient: linear-gradient(0deg,rgba(0,0,0,.7),rgba(0,0,0,.3) 70%,transparent);

.wp-block-gallery {
	display: flex;
    flex-wrap: wrap;

	&.is-cropped {

		.blocks-gallery-image,
		.blocks-gallery-item {

			a,
			img {
				width: 100%;

				@supports ((position: -webkit-sticky) or (position: sticky)) {
					flex: 1;
					height: 100%;
					object-fit: cover;
				}
			}
		}
	}

	&.aligncenter,
	&.alignleft,
	&.alignright {
		display: flex;
	}

	.aligncenter {

		.blocks-gallery-item figure {
			justify-content: center;
		}
	}

	&.alignleft,
	&.alignright {
		max-width: $float-max-width;
		width: 100%;
	}
}

	.blocks-gallery-image,
	.blocks-gallery-item {
		display: flex;
		flex-grow: 1;
		flex-direction: column;
		justify-content: center;
		margin: 0 $gallery-gap $gallery-gap 0;
		position: relative;
		width: calc((100% - $gallery-gap)/2);

		&:nth-of-type(2n) {
			margin-right: 0;
		}

		&:last-child {
			margin-right: 0 !important;
		}

		figure {
			height: 100%;
			margin: 0;

			@supports ((position: -webkit-sticky) or (position: sticky)) {
				align-items: flex-end;
				display: flex;
				justify-content: flex-start;
			}
		}

		img {
			display: block;
			height: auto;
			max-width: 100%;
			width: 100%;

			@supports ((position: -webkit-sticky) or (position: sticky)) {
				width: auto;
			}
		}

		figcaption {
			background: $figcaption-gradient;
			color: $figcaption-text-color;
			font-size: rem(13);
			max-height: 100%;
			overflow: auto;
			padding: rem(40) rem(10) rem(9);
			position: absolute;
				bottom: 0;
			text-align: center;
			width: 100%;

			img {
				display: inline;
			}
		}
	}

	.blocks-gallery-item {
		padding-top: 0;
		padding-left: 0;

		&.has-add-item-button {
			width: 100%;
		}
	}

// Gallery Columns
.wp-block-gallery {

	&.columns-1 {
		.blocks-gallery-image,
		.blocks-gallery-item {
			margin-right: 0;
			width: 100%;
		}
	}

}

@include breakpoint(grid) {
	.wp-block-gallery {

		&.columns-3 {

			.blocks-gallery-image,
			.blocks-gallery-item {
				margin-right: $gallery-gap;
				width: calc((100% - 32px)/3);

				@supports (-ms-ime-align: auto) {
					width: calc((100% - 32px)/3 - 1px)
				}
			}
		}

		&.columns-4 {

			.blocks-gallery-image,
			.blocks-gallery-item {
				margin-right: $gallery-gap;
				width: calc((100% - 48px)/4);

				@supports (-ms-ime-align: auto) {
					width: calc((100% - 48px)/4 - 1px);
				}
			}
		}

		&.columns-5 {

			.blocks-gallery-image,
			.blocks-gallery-item {
				margin-right: $gallery-gap;
				width: calc((100% - 64px)/5);

				@supports (-ms-ime-align: auto) {
					width: calc((100% - 64px)/5 - 1px);
				}
			}
		}

		&.columns-6 {

			.blocks-gallery-image,
			.blocks-gallery-item {
				margin-right: $gallery-gap;
				width: calc((100% - 80px)/6);

				@supports (-ms-ime-align: auto) {
					width: calc((100% - 80px)/6 - 1px);
				}
			}
		}

		&.columns-7 {

			.blocks-gallery-image,
			.blocks-gallery-item {
				margin-right: $gallery-gap;
				width: calc((100% - 96px)/7);

				@supports (-ms-ime-align: auto) {
					width: calc((100% - 96px)/7 - 1px);
				}
			}
		}

		&.columns-8 {

			.blocks-gallery-image,
			.blocks-gallery-item {
				margin-right: $gallery-gap;
				width: calc((100% - 112px)/8);

				@supports (-ms-ime-align: auto) {
					width: calc((100% - 112px)/8 - 1px);
				}
			}
		}

		&.columns-1 .blocks-gallery-image:nth-of-type(1n),
		&.columns-1 .blocks-gallery-item:nth-of-type(1n),
		&.columns-2 .blocks-gallery-image:nth-of-type(2n),
		&.columns-2 .blocks-gallery-item:nth-of-type(2n),
		&.columns-3 .blocks-gallery-image:nth-of-type(3n),
		&.columns-3 .blocks-gallery-item:nth-of-type(3n),
		&.columns-4 .blocks-gallery-image:nth-of-type(4n),
		&.columns-4 .blocks-gallery-item:nth-of-type(4n),
		&.columns-5 .blocks-gallery-image:nth-of-type(5n),
		&.columns-5 .blocks-gallery-item:nth-of-type(5n),
		&.columns-6 .blocks-gallery-image:nth-of-type(6n),
		&.columns-6 .blocks-gallery-item:nth-of-type(6n),
		&.columns-7 .blocks-gallery-image:nth-of-type(7n),
		&.columns-7 .blocks-gallery-item:nth-of-type(7n),
		&.columns-8 .blocks-gallery-image:nth-of-type(8n),
		&.columns-8 .blocks-gallery-item:nth-of-type(8n) {
			margin-right: 0;
		}
	}
}
