/**
 * Form search
 *
 * The WordPress search form, as called by `get_search_form()` or
 * via the Search widget.
 *
 * @package  Embark
 * @since    1.0.0
 */

.search-form {

	&__inner {
		display: flex;
	}

	// Text input.
	.search-field {
		border-top-right-radius: 0;
		border-bottom-right-radius: 0;
	}

	// Submit button.
	.search-submit {
		border-top-left-radius: 0;
		border-bottom-left-radius: 0;
	}

	label {
		margin: 0;
	}

}