.wp-block-file {

	&.aligncenter {
		text-align: center;
	}

	&.alignright {
		text-align: right;
	}

	.wp-block-file__button {
		background: $secondary-color;
		border-radius: rem(4);
		color: #fff;
		font-size: rem(14);
		padding: rem(6) rem(12);
		text-decoration: none;

		&:hover {
			opacity: .75;
		}
	}

	* +.wp-block-file__button {
		margin-left: $gap-md;
	}
}

