/**
 * Social Media Menu.
 *
 * Displays a WordPress navigation menu for the menu location "Social Media Links". 
 * There is a hook in the walker of this menu locaion that wraps the text with the
 * .screen-reader-text class, and adds an SVG use element based on the URL.
 *
 * @since 1.0.0
 */
.social-media-menu {
	margin: 0 rem(-5);

	.menu-item {
		display: inline-block;
		margin: 0 rem(5);
	}

	a {
		height: rem(32);
		width: rem(32);
	}

	.icon {
		display: block;
		fill: currentColor;
		height: rem(32);
		transition-property: fill;
		width: rem(32);
	}
}