.wp-block-cover,
.wp-block-cover-image {
    align-items: center;
    background-color: #000;
    background-size: cover;
    background-position: 50%;
    display: flex;
    justify-content: center;
    min-height: rem(430);
    position: relative;
    overflow: hidden;
	width: 100%;

	&.has-left-content {
		justify-content: flex-start;

		.wp-block-cover-image-text,
		.wp-block-cover-text,
		h2 {
			margin-left: 0;
    		text-align: left;
		}
	}

	&.has-right-content {
		justify-content: flex-end;

		.wp-block-cover-image-text,
		.wp-block-cover-text,
		h2 {
			margin-left: 0;
    		text-align: right;
		}
	}
	
	.wp-block-cover-image-text,
	.wp-block-cover-text,
	h2 {
		color: #fff;
		font-size: 2em;
		line-height: 1.25;
		margin-right: auto;
		margin-left: auto;
		margin-bottom: 0;
		max-width: rem(610);
		padding: $gap-md;
		text-align: center;
		z-index: 1;
	}

	&.has-parallax {
		background-attachment: fixed;

		@supports (-webkit-overflow-scrolling: touch) {
			background-attachment: scroll;
		}
	}

	&.has-background-dim {
	
		&::before {
			background-color: inherit;
			content: "";
			opacity: .5;
			position: absolute;
				top: 0;
				left: 0;
				bottom: 0;
				right: 0;
				z-index: 1;
		}
	}

	&.has-background-dim-10 {
	
		&::before {
			opacity: .1;
		}
	}

	&.has-background-dim-20 {
	
		&::before {
			opacity: .2;
		}
	}

	&.has-background-dim-30 {
	
		&::before {
			opacity: .3;
		}
	}

	&.has-background-dim-40 {
	
		&::before {
			opacity: .4;
		}
	}

	&.has-background-dim-50 {
	
		&::before {
			opacity: .5;
		}
	}

	&.has-background-dim-60 {
	
		&::before {
			opacity: .6;
		}
	}

	&.has-background-dim-70 {
	
		&::before {
			opacity: .7;
		}
	}

	&.has-background-dim-80 {
	
		&::before {
			opacity: .8;
		}
	}

	&.has-background-dim-90 {
	
		&::before {
			opacity: .9;
		}
	}

	&.has-background-dim-1 {
	
		&::before {
			opacity: 1;
		}
	}

	&.aligncenter,
	&.alignleft,
	&.alignright {
		display: flex;
	}

	&.alignleft,
	&.alignright {
		max-width: rem(305);
		width: 100%;
	}

	&::after {
		display: block;
		content: "";
		font-size: 0;
		min-height: inherit;
		
		@supports ((position: -webkit-sticky) or (position: sticky)) {
			content: none;
		}
	}

	.wp-block-cover__inner-container,
	.wp-block-cover__inner-container {
		width: calc(100% - #{rem(70)});
		z-index: 1;
		color: #f8f9f9;
	}

	.wp-block-subhead,
	h1,
	h2,
	h3,
	h4,
	h5,
	h6,
	p,
	a {
		color: inherit;
	}
}

.wp-block-cover__video-background {
    height: 100%;
    position: absolute;
		top: 50%;
		left: 50%;
		z-index: 0;
	transform: translateX(-50%) translateY(-50%);
    width: 100%;
    object-fit: cover;
}