// sass-lint:disable no-qualifying-elements
// sass-lint:disable no-ids


// Override Project Typography

.gform_fields ,
.gfield_radio ,
.gfield_checkbox {
	padding-left: 0;
}

.gform_fields {
	@extend %no-list-style;
}


// Gravity Forms - Defaults

.gform_wrapper {
	margin-bottom: $gap-md;
}

.gfield {
	clear: both;
	padding-bottom: rem(15);
}

.gform_description {
	display: block;
	margin-bottom: rem(30);
}

// .screen-reader-text is used on upload fields ("Accepted file types: jpg, gif...")
.screen-reader-text {
  display: block;
  font-size: 13px;
  font-weight: 700;
  margin-top: 7px;
}

.instruction ,
.gfield_description {
	clear: both;
	font-size: 85%;
}

.instruction {
	font-style: italic;
}

.gfield_required {
	color: $form-required;
	margin-left: 3px;
	position: relative;
	top: -4px;
}

.validation_error,
.validation_message,
.gfield_error input {
	border-color: $form-required;
	color: $form-required;
	font-weight: 600;
}

.validation_message {
	margin-top: 5px;
}

.validation_error {
	margin-bottom: 20px;
}

.gfield_error {

	input ,
	textarea ,
	.selector
	select[multiple="multiple"] {
		border-color: $form-required;
		color: $form-required;
		font-weight: 600;
	}

}

.gform_footer {
	clear: both;
}

.gform_ajax_spinner {
	display: none !important;
}

// Gravity Forms - Radio Button / Check Boxes

.gfield_radio ,
.gfield_checkbox {
	padding-left: 0;

	label {
		display: inline;
	}

}

.gform_fields .gfield_checkbox li,
.ginput_container_consent {
  @extend %pretty-checkbox;
}

.gform_fields .gfield_radio li {
  @extend %pretty-radio;
}

// Gravity Forms - Complex Fields

.ginput_complex {
	overflow: hidden;

	label {
		display: block;
		font-size: 85%;
	}

	input:not([type="checkbox"]):not([type="radio"]) {
		width: 100%;
	}

	.ginput_right,
	.ginput_left {
		float: left;
		width: 49%;
	}

	.ginput_right {
		margin-left: 2%;
	}

}

// Gravity Forms - Complex Fields (Name Fields)

.name_prefix ,
.name_first ,
.name_middle ,
.name_last ,
.name_suffix {
	float: left;
	width: 49%;
}

.gf_name_has_5 span {
	margin-left: 1%;
	width: 19.2%;
}

.gf_name_has_4 span {
	margin-left: 2%;
	width: 23.5%;
}

.gf_name_has_3 span {
	margin-left: 2%;
	width: 32%;
}

.gf_name_has_2 span {
	margin-left: 2%;
	width: 49%;
}

.gf_name_has_5 span:first-child ,
.gf_name_has_4 span:first-child ,
.gf_name_has_3 span:first-child ,
.gf_name_has_2 span:first-child {
	margin-left: 0;
}

// Gravity Forms - Complex Fields (Address Fields)

.ginput_container_address {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;

	.ginput_full {
		width: 100%;
	}

	.ginput_left,
	.ginput_right {
		width: 49%;
	}
}

// Half/Third Ready Classes

.gform_fields {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
}

// Gravity Forms - Sizes

.gfield {
	width: 100%;

	.small {
		width: 25%;
	}

	.medium {
		width: 49%;
	}

	.large {
		width: 100%;
	}
}

.gf_left_half ,
.gf_right_half ,
.gf_left_third ,
.gf_middle_third ,
.gf_right_third {

	.small,
	.medium,
	.large {
		width: 100% !important;
	}
}

// halves
.gf_left_half ,
.gf_right_half {
	width: 49%;
}

// thirds
.gf_left_third ,
.gf_middle_third ,
.gf_right_third {
	width: 32%;
}

// Gravity Forms - Date Fields And Time Fields

.gfield_date_month ,
.gfield_date_day ,
.gfield_date_year ,
.gfield_date_dropdown_month ,
.gfield_date_dropdown_day ,
.gfield_date_dropdown_year {
	float: left;
	margin-left: 2%;
	width: rem(100);

	input {
		display: block;
		width: 100%;
	}

}

.gfield_date_dropdown_month ,
.gfield_date_month {
	margin-left: 0;
}

// time
.gfield_time_hour ,
.gfield_time_minute ,
.gfield_time_ampm {
	float: left;

	input {
		width: rem(54);
	}

	label	{
		display: block;
	}

}

.gfield_time_minute	{
	margin-left: rem(4);
}

.gfield_time_ampm {
	margin-left: rem(12);
	width: rem(70);
}

// Gravity Forms - File Upload (Multiple Files)

.gform_drop_area {
	border: 2px dashed #ddd;
	color: #aaa;
	margin-bottom: 10px;
	padding: rem(25);
	text-align: center;
}

// Gravity Forms - Date Picker

.datepicker {
	max-width: rem(140);
}

.ui-datepicker {
	box-shadow: 0 0 3px 2px rgba(0,0,0,.2);
	font-size: 9px;

	a {
		text-decoration: none;

	}

}

.ui-datepicker-header {
	background-color: #fff; /* set the header background color */
	color: #e0e0e0;
	font-weight: 700;
	box-shadow: inset 0 1px 1px 0 rgba(250, 250, 250, .2);
	line-height: 30px;
	min-height: 30px !important;
	border-width: 1px 0 0 0;
	border-style: solid;
	border-color: #666;
}

.ui-datepicker-title {
	text-align: center;
}

.ui-datepicker-title select {
	border: 1px solid #e0e0e0;
	font-size: rem(11);
	margin-left: 2px;
	margin-right: 2px;
	margin-top: 2.5%;
}

.ui-datepicker-prev,
.ui-datepicker-next {
	display: inline-block;
	height: 30px;
	text-align: center;
	cursor: pointer;
	overflow: hidden;
	width: 30px;
}

.ui-datepicker-prev {
	float: left;
	background-position: center -30px;
}

.ui-datepicker-next {
	float: right;
	background-position: center 0;
}

.ui-datepicker thead {
	background-color: #f7f7f7;
	background: linear-gradient(to bottom,  #f7f7f7 0%,#f1f1f1 100%);
	border-bottom: 1px solid #bbb;
}

.ui-datepicker th {
	text-transform: uppercase;
	text-align: center;
	font-size: 6pt;
	padding: 5px 0;
	color: #fff;
}

.ui-datepicker tbody td {
	padding: 0;
	border-top: 1px solid #bbb;
	border-right: 1px solid #bbb;
}

.ui-datepicker tbody td:last-child {
	border-right: none;
}

.ui-datepicker tbody tr {
	border-bottom: 1px solid #bbb;
}

.ui-datepicker tbody tr:last-child {
	border-bottom: none;
}

.ui-datepicker td span,
.ui-datepicker td a {
	display: inline-block;
	font-weight: 700;
	text-align: center;
	width: 30px;
	height: 30px;
	line-height: 30px;
	color: #666666;

	filter: dropshadow(color=#fff, offx=1, offy=1);
}

.ui-datepicker-calendar .ui-state-default {
	background-color: #ededed;
	background: linear-gradient(to bottom,  #ededed 0%,#dedede 100%);
	box-shadow: inset 1px 1px 0 0 rgba(250, 250, 250, .5);
}

.ui-datepicker-calendar .ui-state-hover {
	background: #f7f7f7;
}

.ui-datepicker-calendar .ui-state-active {
	background: #fff2aa; /* set the active date background color */
	border: 1px solid #c19163; /* set the active date border color */
	color: #666; /* set the active date font color */
	box-shadow: inset 0 0 10px 0 rgba(0, 0, 0, .1);


	position: relative;
	margin: -1px;
}

.ui-datepicker-unselectable .ui-state-default {
	background: #f4f4f4;
	color: #b4b3b3;
}

.ui-datepicker-calendar td:first-child .ui-state-active {
	width: 29px;
	margin-left: 0;
}

.ui-datepicker-calendar td:last-child .ui-state-active {
	width: 29px;
	margin-right: 0;
}

.ui-datepicker-calendar tr:last-child .ui-state-active {
	height: 29px;
	margin-bottom: 0;
}

td.ui-datepicker-unselectable.ui-state-disabled {
	background-color: #d7d7d7;
}

table.ui-datepicker-calendar {
	margin: 0 0 0 0 !important;
}

body div#ui-datepicker-div[style] {
	z-index: 9999 !important;
}


// Gravity Forms - Progress Bar

.gf_progressbar {
	border-radius: 3px;
	background: $gray;
	padding: rem(5);
}

.gf_progressbar_percentage {
	border-radius: 3px;
	background: $primary-color;
	color: #fff;
	font-weight: 600;
	padding: 0 5px;
	text-align: center;
}

// Gravity Forms - List

.ginput_list {

	.gfield_list {

		tr:nth-child(even) {
			background: none;
		}

		td {
      border: none;
			padding: rem(10) 0;

			&.gfield_list_icons {
				padding-left: rem(10);

				img {
          display: inline-block;
          margin-right: rem(7);
				}
			}
		}
	}
}

.field_description_below {

	.gfield_description {
		margin-top: rem(4);
	}

}

// Gravity Forms Widget

.gform_widget {

	.top_label input ,
	.uploader {
		width: 100%;
	}

	.top_label [type="submit"] {
		width: auto;
	}

	.uploader span.action {
		position: absolute;
			right: 0;
			top: 0;
	}

}

// Hide label from the font-end.
.hidden_label {

	.gfield_label {
		clip: rect(1px,1px,1px,1px);
		height: 1px;
		overflow: hidden;
		position: absolute;
		width: 1px;
	}
}

// Media Queries

@include breakpoint(xs, max) {

	.gf_right_half ,
	.gf_name_has_2 span {
		margin-left: 0;
	}


	.ginput_complex {

		> span {
			width: 100%;

			+ span {
				margin-top: rem(15);
			}

		}

		.name_first ,
		.name_last ,
		.ginput_left,
		.ginput_right {
			width: 100%;
		}

		.ginput_right {
			margin-left: 0;
		}

	}

	.gf_left_half,
	.gf_right_half {
		width: 100%;

		input {
			width: 100%;
		}
	}

	.gfield {
		margin-top: 0;

		input,
		.selector {
			width: 100%;
		}
	}

	.gform_wrapper {
		.medium ,
		.large {
			width: 100% !important;
		}
	}

}

// Add Chosen For Multiselect Enhanced Ui

$chosen-sprite: url("data:image/png;base64,") !default;
$chosen-sprite-retina: url("data:image/png;base64,") !default;

/* @group Base */
.chosen-container {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  font-size: 13px;
  user-select: none;
  * {
    box-sizing: border-box;
  }
  .chosen-drop {
    position: absolute;
    top: 100%;
    z-index: 1010;
    width: 100%;
    border: 1px solid #aaa;
    border-top: none;
    background: #fff;
    box-shadow: 0 4px 5px rgba(#000,.15);
    clip: rect(0,0,0,0);
  }
  &.chosen-with-drop .chosen-drop {
    clip: auto;
  }
  a {
    cursor: pointer;
  }

  .search-choice,
  .chosen-single {
    .group-name {
      margin-right: 4px;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      font-weight: normal;
      color: #999999;
      &:after {
        content: ":";
        padding-left: 2px;
        vertical-align: top;
      }
    }
  }
}
/* @end */

/* @group Single Chosen */
.chosen-container-single {
  .chosen-single {
    position: relative;
    display: block;
    overflow: hidden;
    padding: 0 0 0 8px;
    height: 25px;
    border: 1px solid #aaa;
    border-radius: 5px;
    background-color: #fff;
    background: linear-gradient(#fff 20%, #f6f6f6 50%, #eee 52%, #f4f4f4 100%);
    background-clip: padding-box;
    box-shadow: 0 0 3px #fff inset, 0 1px 1px rgba(#000,.1);
    color: #444;
    text-decoration: none;
    white-space: nowrap;
    line-height: 24px;
  }
  .chosen-default {
    color: #999;
  }
  .chosen-single span {
    display: block;
    overflow: hidden;
    margin-right: 26px;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .chosen-single-with-deselect span {
    margin-right: 38px;
  }
  .chosen-single abbr {
    position: absolute;
    top: 6px;
    right: 26px;
    display: block;
    width: 12px;
    height: 12px;
    background: $chosen-sprite -42px 1px no-repeat;
    font-size: 1px;
    &:hover {
      background-position: -42px -10px;
    }
  }
  &.chosen-disabled .chosen-single abbr:hover {
    background-position: -42px -10px;
  }
  .chosen-single div {
    position: absolute;
    top: 0;
    right: 0;
    display: block;
    width: 18px;
    height: 100%;
    b {
      display: block;
      width: 100%;
      height: 100%;
      background: $chosen-sprite no-repeat 0 2px;
    }
  }
  .chosen-search {
    position: relative;
    z-index: 1010;
    margin: 0;
    padding: 3px 4px;
    white-space: nowrap;

    input[type="text"] {
      margin: 1px 0;
      padding: 4px 20px 4px 5px;
      width: 100%;
      height: auto;
      outline: 0;
      border: 1px solid #aaa;
      background: $chosen-sprite no-repeat 100% -20px;
      font-size: 1em;
      font-family: sans-serif;
      line-height: normal;
      border-radius: 0;
    }
  }
  .chosen-drop {
    margin-top: -1px;
    border-radius: 0 0 4px 4px;
    background-clip: padding-box;
  }
  &.chosen-container-single-nosearch .chosen-search {
    position: absolute;
    clip: rect(0,0,0,0);
  }
}
/* @end */

/* @group Results */
.chosen-container .chosen-results {
  color: #444;
  position: relative;
  overflow-x: hidden;
  overflow-y: auto;
  margin: 0 4px 4px 0;
  padding: 0 0 0 4px;
  max-height: 240px;
  li {
    display: none;
    margin: 0;
    padding: 5px 6px;
    list-style: none;
    line-height: 15px;
    word-wrap: break-word;
    &.active-result {
      display: list-item;
      cursor: pointer;
    }
    &.disabled-result {
      display: list-item;
      color: #ccc;
      cursor: default;
    }
    &.highlighted {
      background-color: #3875d7;
      background-image: linear-gradient(#3875d7 20%, #2a62bc 90%);
      color: #fff;
    }
    &.no-results {
      color: #777;
      display: list-item;
      background: #f4f4f4;
    }
    &.group-result {
      display: list-item;
      font-weight: 700;
      cursor: default;
    }
    &.group-option {
      padding-left: 15px;
    }
    em {
      font-style: normal;
      text-decoration: underline;
    }
  }
}
/* @end */

/* @group Multi Chosen */
.chosen-container-multi {
  .chosen-choices {
    position: relative;
    overflow: hidden;
    margin: 0;
    padding: 0 5px;
    width: 100%;
    height: auto;
    border: 1px solid #aaa;
    background-color: #fff;
    background-image: linear-gradient(#eee 1%, #fff 15%);
    cursor: text;
  }
  .chosen-choices li {
    float: left;
    list-style: none;
    &.search-field {
      margin: 0;
      padding: 0;
      white-space: nowrap;
      input[type="text"] {
        margin: 1px 0;
        padding: 0;
        height: 25px;
        outline: 0;
        border: none !important;
        background: transparent !important;
        box-shadow: none;
        color: #999;
        font-size: 100%;
        font-family: sans-serif;
        line-height: normal;
        border-radius: 0;
        width: 25px;
      }
    }
    &.search-choice {
      position: relative;
      margin: 3px 5px 3px 0;
      padding: 3px 20px 3px 5px;
      border: 1px solid #aaa;
      max-width: 100%;
      border-radius: 3px;
      background-color: #eeeeee;
      background-image: linear-gradient(#f4f4f4 20%, #f0f0f0 50%, #e8e8e8 52%, #eee 100%);
      background-size: 100% 19px;
      background-repeat: repeat-x;
      background-clip: padding-box;
      color: #333;
      line-height: 13px;
      cursor: default;
      span {
        word-wrap: break-word;
      }
      .search-choice-close {
        position: absolute;
        top: 4px;
        right: 3px;
        display: block;
        width: 12px;
        height: 12px;
        background: $chosen-sprite -42px 1px no-repeat;
        font-size: 1px;
        &:hover {
          background-position: -42px -10px;
        }
      }
    }
    &.search-choice-disabled {
      padding-right: 5px;
      border: 1px solid #ccc;
      background-color: #e4e4e4;
      background-image: linear-gradient(#f4f4f4 20%, #f0f0f0 50%, #e8e8e8 52%, #eee 100%);
      color: #666;
    }
    &.search-choice-focus {
      background: #d4d4d4;
      .search-choice-close {
        background-position: -42px -10px;
      }
    }
  }
  .chosen-results {
    margin: 0;
    padding: 0;
  }
  .chosen-drop .result-selected {
    display: list-item;
    color: #ccc;
    cursor: default;
  }
}
/* @end */

/* @group Active  */
.chosen-container-active {
  .chosen-single {
    border: 1px solid #5897fb;
  }
  &.chosen-with-drop {
    .chosen-single {
      border: 1px solid #aaa;
      border-bottom-right-radius: 0;
      border-bottom-left-radius: 0;
      background-image: linear-gradient(#eee 20%, #fff 80%);
    }
    .chosen-single div {
      border-left: none;
      background: transparent;
      b {
        background-position: -18px 2px;
      }
    }
  }
  .chosen-choices {
    border: 1px solid #5897fb;
    li {
      &.search-field input[type="text"] {
        color: #222 !important;
      }
    }
  }
}
/* @end */

/* @group Disabled Support */
.chosen-disabled {
  opacity: .5 !important;
  cursor: default;
  .chosen-single {
    cursor: default;
  }
  .chosen-choices .search-choice .search-choice-close {
    cursor: default;
  }
}
/* @end */

/* @group Right to Left */
.chosen-rtl {
  text-align: right;
  .chosen-single {
    overflow: visible;
    padding: 0 8px 0 0;
  }
  .chosen-single span {
    margin-right: 0;
    margin-left: 26px;
    direction: rtl;
  }
  .chosen-single-with-deselect span {
    margin-left: 38px;
  }
  .chosen-single div {
    right: auto;
    left: 3px;
  }
  .chosen-single abbr {
    right: auto;
    left: 26px;
  }
  .chosen-choices li {
    float: right;
    &.search-field input[type="text"] {
      direction: rtl;
    }
    &.search-choice {
      margin: 3px 5px 3px 0;
      padding: 3px 5px 3px 19px;
      .search-choice-close {
        right: auto;
        left: 4px;
      }
    }
  }
  &.chosen-container-single .chosen-results {
    margin: 0 0 4px 4px;
    padding: 0 4px 0 0;
  }
  .chosen-results li {
    &.group-option {
      padding-right: 15px;
      padding-left: 0;
    }
  }
  &.chosen-container-active.chosen-with-drop .chosen-single div {
    border-right: none;
  }
  .chosen-search input[type="text"] {
    padding: 4px 5px 4px 20px;
    background: $chosen-sprite no-repeat -30px -20px;
    direction: rtl;
  }
  &.chosen-container-single {
    .chosen-single div b {
      background-position: 6px 2px;
    }
    &.chosen-with-drop {
      .chosen-single div b {
        background-position: -12px 2px;
      }
    }
  }
}

/* @end */

/* @group Retina compatibility */
@media only screen and (-webkit-min-device-pixel-ratio: 1.5), only screen and (min-resolution: 144dpi), only screen and (min-resolution: 1.5dppx) {
  .chosen-rtl .chosen-search input[type="text"],
  .chosen-container-single .chosen-single abbr,
  .chosen-container-single .chosen-single div b,
  .chosen-container-single .chosen-search input[type="text"],
  .chosen-container-multi .chosen-choices .search-choice .search-choice-close,
  .chosen-container .chosen-results-scroll-down span,
  .chosen-container .chosen-results-scroll-up span {
    background-image: $chosen-sprite-retina !important;
    background-size: 52px 37px !important;
    background-repeat: no-repeat !important;
  }
}
/* @end */

// sass-lint:enable no-qualifying-elements
// sass-lint:enable no-ids
