.wp-block-image {

	&:not([class*="align"]) {
		
		figcaption {
			text-align: left;
		}
	}

	.alignleft {
		margin-right: $gap-lg;
	}

	.alignright {
		margin-left: $gap-lg;
	}

	// Ensure images marked as full width or wide span 100% of the container.
	&.alignfull,
	&.alignwide {

		img {
			max-width: none;
			width: 100%;
		}
	}
}