/**
 * WP Core Widget: Recent Posts
 *
 * @package  Embark
 * @since    1.0.0
 */
.widget_recent_entries {
	
	ul {
		margin: 0;
		padding: 0;
	}

	// Remove margins and padding on listitems.
	li {
		@extend %#{$btn-class}-text;
		list-style: none;
		margin: 0;
		padding: 0;

		+ li {
			border-top: 1px solid $gray-l;
			margin-top: rem(15);
			padding-top: rem(15);
		}

	}

	a {
		text-decoration: none;
	}

	.post-date {
		@include font-family( $primary-font, 400 );
		display: block;
		font-size: rem(14);
		text-transform: none;
	}

}