/**
 * Site footer component
 *
 * @see     config/mixins/_media-queries.scss        Media query mixins.
 * @see     config/variables/_media-sizes.scss       Media query variables.
 *
 * @package Embark
 * @since   1.0.0
 */

.site-footer {
	padding: $gap-lg 0;

	// Flex container.
	&__inner {

		@include breakpoint(sm, max) {
			text-align: center;
		}

		@include breakpoint(sm) {
			align-items: center;
			display: flex;
			flex-direction: row-reverse;
			justify-content: space-between;
			width: 100%;
		}

	}

	&__social {

		@include breakpoint(sm, max) {
			margin-bottom: rem(16);
		}

	}

}
