/**
 * LIST STYLES
 * Base styles for ul, ol, li and multi-column lists.
 *
 * @since 1.0.0
 */

@mixin list-styles {
	list-style: none;
	margin: em($list-vertical-gap) 0;
	padding: 0;

	&.consecutive {
		counter-reset: auto;
	}

	&:first-child {
		margin-top: 0;
	}

	&:last-child {
		margin-bottom: 0;
	}

	.editor-styles & {
		overflow: hidden;
	}

	li,
	li li:first-child {
		padding-top: rem($li-gap);
	}

	li:first-child {
		padding-top: 0;
	}

	li {
		padding-left: rem($list-indent-amount);
		position: relative;

		&:before {
			content: "\2022";
			color: $ul-bullet-color;
			display: inline-block;
			position: absolute;
			left: rem($list-indent-amount * .5);
			text-shadow: 0 0 1px $primary-color;
			width: rem($list-bullet-width);
		}
	}

	ul,
	ol {
		list-style: none;
		padding-left: 0;
		margin-top: 0;
	}
}

@mixin ol-styles {
	counter-reset: li;

	ol {
		counter-reset: li;
	}

	> li:before,
	ol > li:before {
		content: "." counter(li);
		counter-increment: li;
		direction: rtl;
		font-weight: 500;
		left: rem($list-indent-amount * .25);
		text-align: right;
	}

	@for $i from 2 through 25 {
		&[start="#{$i}"] {
			counter-reset: li ($i - 1) ;
		}
	}
}

.wp-block-list {
	@include list-styles;

	ol {
		@include ol-styles;
	}
}

ol {

	&.wp-block-list {
		@include ol-styles;
	}

	ul {
		@include list-styles;
	}
}

// Editor specific styles.
@include jacket(editor) {

	.block-library-list {

		ul,
		ol {
			@include list-styles;
		}

		ol {
			@include ol-styles;
		}
	}
}
