/**
 * Icon List item Pattern
 *
 * Defines a set of classes to apply to specific markup intended for
 * displaying icons in a list format.
 *
 * @package  Embark
 * @since    1.2.0
 */
 // ACF defined icon sizes
 $icon-width-sm: 50px;
 $icon-width-md: 75px;
 $icon-width-lg: 100px;

 // mixin determines width of icon-list__media
 @mixin icon-media-flex-width( $icon-media-flex-width ) {
	max-width: $icon-media-flex-width;
	@include breakpoint(xs) {
		flex: 1 $icon-media-flex-width;
	}
 }

 // mixin determines width of icon-list__body
 @mixin icon-body-flex-width( $icon-body-flex-width ) {
	@include breakpoint(xs) {
		flex: 1 calc( 100% - ( #{$icon-body-flex-width} + 15px ));
		max-width: calc( 100% - ( #{$icon-body-flex-width} + 15px ));
	}
 }

.icon-list {
	color: $base-font-color;
	position: relative;
	text-decoration: none;
	transition-property: background-color, border-color, box-shadow, color;
	margin-bottom: 0;

	// icon list item.
	&__item {
		display: flex;
		flex-flow: row wrap;
		flex: 1 100%;
		max-width: 100%;
		padding-left: 0;
		padding-top: 0;
		margin-bottom: 1.25rem;

		&:before {
			content: "";
		}
	}

	// icon media.
	&__media {
		overflow: hidden;
		margin-bottom: 12px;
		flex: 1 100%;

		@include breakpoint(xs) {
			margin-right: 15px;
		}

		img,
		svg {
			position: relative;
			max-width: 100%;
			height: auto;
		}
	}

	// icon-list body. The container for all content in the con-list (excluding the icon-list__footer).
	&__body {
		max-width: 100%;
		flex: 1 100%;

	}

	&.icon-sm {
		.icon-list__media {
			@include icon-media-flex-width($icon-width-sm);
		}
		&:not( .grid ){
			.icon-list__body {
				@include icon-body-flex-width($icon-width-sm);
			}
		}
	}
	&.icon-md {
		.icon-list__media {
			@include icon-media-flex-width($icon-width-md);
		}
		&:not( .grid ){
			.icon-list__body {
				@include icon-body-flex-width($icon-width-md);
			}
		}

	}
	&.icon-lg {
		.icon-list__media {
			@include icon-media-flex-width($icon-width-lg);
		}
		&:not( .grid ){
			.icon-list__body {
				@include icon-body-flex-width($icon-width-lg);
			}
		}
	}

	// Excerpt. Placed inside icon-list__body. Container for paragraph elements.
	&__excerpt {
		color: rgba( $gray-d, $muted );
	}

	// Label. Intended to have an h6 style.
	&__label {
		@extend %h5; // from elements/_headings.scss
		color: $base-font-color;
	}

	// Button text. This may be a span tag, so it needs to be styled like a link when icon-list is hovered.
	.btn {
		color: $primary-color !important;
		padding: 0;
		background-color: transparent;
		border: none;
		text-transform: uppercase;
		margin-top: 24px;

		@include breakpoint(xs) {
			border: 2px solid $primary-color;
			border-radius: 4px;
			padding: 8px 10px;
		}

	}

	@include jacket(non-critical) {

		// Allow for all these props to be transitioned.
		.btn {
			transition-property: color;

			&:hover {
				@include breakpoint(xs) {
					background-color: $primary-color;
					color: white !important;
				}
			}

		}

	}

}
