/**
 * Allow font-size to adjust based on screen size.
 *
 * Font-size will flex between the $min-font-size and $max-font-size
 * values when the viewport is between $min-vw and $max-vw. When the
 * viewport is below $min-vw, the $min-font-size will be applied.
 * When the viewport is above $max-vw, the #max-font-size will be
 * applied.
 *
 * $min-vw: The viewport size to start applying fluid type rules
 * $max-vw: The viewport size to stop applying fluid type rules
 * $min-font-size: The minimum font size to apply when $min-vw is hit.
 * $max-font-size: The maximum font size to apply when $max-vw is hit.
 */
@mixin fluid-type($min-vw, $max-vw, $min-font-size, $max-font-size) {
	font-size: rem($min-font-size);
	@include breakpoint($min-vw) {
		font-size: calc(#{rem($min-font-size)} + #{strip-unit($max-font-size - $min-font-size)} * ((100vw - #{rem(strip-unit($min-vw))}) / #{strip-unit($max-vw - $min-vw)}));
	}
	@include breakpoint($max-vw) {
		font-size: rem($max-font-size);
	}
}
