.wp-block-video {

	&.aligncenter {
		text-align: center
	}

	video {
		max-width: 100%;
	}

	@supports ((position: -webkit-sticky) or (position: sticky)) {
		[poster] {
			object-fit: cover;
		}
	}

	figcaption {
		color: $gray-d;
		font-size: rem(14);
		margin-top: $gap-md;
		margin-bottom: $gap-md;
		text-align: center;
	}
}