.site {

	@if $sticky-footer {
		display: flex;
		min-height: 100%;
		flex-direction: column;

		.ie & {
			height: 100%;
		}
	}

	@include jacket(non-critical) {

		&::before {
			content: " ";
			background: #000;
			opacity: 0;
			position: fixed;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			z-index: -1;
			transition-property: opacity, z-index;
		}

		.html--menu-open & {

			@include breakpoint(xs) {

				&::before {
					opacity: .6;
					z-index: $zindex-nav - 1;
				}
			}
		}
	}
}
